import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';
import {Link} from 'react-router-dom';
import QuickClick from "./QuickClick"

const useStyles = makeStyles({
    list: {
        width: 250,
    },
    fullList: {
        width: 'auto',
    },
});

const scholarUrl = 'https://scholar.google.com/citations?user=lOOmgEgAAAAJ&hl=en';
const githubUrl = 'https://github.com/Aaron-Zhao123/';
const linkdinUrl = 'https://www.linkedin.com/in/yiren-aaron-zhao-baa8b5116/';

export default function TemporaryDrawer() {
    const classes = useStyles();
    const [state, setState] = React.useState({
        top: false,
        left: false,
        bottom: false,
        right: false,
    });

    const toggleDrawer = (anchor, open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }

        setState({ ...state, [anchor]: open });
    };

    const list = () => (
        <div
            className={clsx(classes.list, {
                [classes.fullList]: false,
            })}
            role="presentation"
            onClick={toggleDrawer('left', false)}
            onKeyDown={toggleDrawer('left', false)}
        >
            <Divider />
            <List>
                <ListItem button key='main' component={Link} to="/">
                    <ListItemText primary={'Main page'} />
                </ListItem>
                <ListItem button key='resume' component={Link} to="/resume">
                    <ListItemText primary={'Resume'} />
                </ListItem>
                <ListItem button key='pub' component={Link} to="/pub">
                    <ListItemText primary={'Publication'} />
                </ListItem>
                <ListItem button key='talks' component={Link} to="/talks">
                    <ListItemText primary={'Talks'} />
                </ListItem>
                <ListItem button key='projects' component={Link} to="/projects">
                    <ListItemText primary={'Projects'} />
                </ListItem>
                <ListItem button key='notes' component={Link} to="/notes">
                    <ListItemText primary={'Notes'} />
                </ListItem>
            </List>
        </div>
    );

    return (
        <div>
            {['left'].map((anchor) => (
                <React.Fragment key={anchor}>
                    <Button onClick={toggleDrawer(anchor, true)} variant="outlined">Menu</Button>
                    <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
                        {list(anchor)}
                    </Drawer>
                    <QuickClick/>
                </React.Fragment>
            ))}
        </div>
    );
}
